import * as React from "react";
import PropTypes from "prop-types";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";

import itemOne from "../../static/img/itemOne.png";
import itemTwo from "../../static/img/itemTwo.jpg";
import itemTree from "../../static/img/itemTree.jpg";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    "aria-controls": `vertical-tabpanel-${index}`,
  };
}

export default function VerticalTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box
      sx={{
        flexGrow: 1,
        bgcolor: "background.paper",
        display: "flex",
        height: 410,
      }}
    >
      <Tabs
        orientation="vertical"
        variant="scrollable"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        sx={{ borderRight: 1, borderColor: "divider" }}
      >
        <Tab label="One" {...a11yProps(0)} />
        <Tab label="Two" {...a11yProps(1)} />
        <Tab label="Three" {...a11yProps(2)} />
        <Tab label="Four" {...a11yProps(3)} />
        <Tab label="Five" {...a11yProps(4)} />
      </Tabs>
      <TabPanel value={value} index={0}>
        <img style={{ width: 800, height: 360 }} src={itemOne} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <img style={{ width: 800, height: 360 }} src={itemTwo} />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <img style={{ width: 800, height: 360 }} src={itemTree} />
      </TabPanel>
      <TabPanel value={value} index={3}>
        <img style={{ width: 800, height: 360 }} src={itemTree} />
      </TabPanel>
      <TabPanel value={value} index={4}>
        <img style={{ width: 800, height: 360 }} src={itemTree} />
      </TabPanel>
    </Box>
  );
}
